export const userHelper = (items, userId, objProp, objPropName) => {
    return items.map(i => {
        if (i[objPropName] === userId) {
            return { ...i, ...objProp }
        }
        return i;
    })
}
export const likeHelper = (items, userId, objProp, objPropSecond, objPropName) => {
    return items.map(i => {
        if (i[objPropName] === userId) {
            return { ...i, ...objProp, ...objPropSecond }
        }
        return i;
    })
}