/* import { authAPI } from "../service/service"; */

let AUTH_ME = 'social-network/loginReducer/LOGIN-ME';

let initialState = {
    email: '',
    password: '',
    rememberMe: false,
    captcha: false
}

export let loginReducer = (state = initialState, action) => {
    switch (action.type) {
        case AUTH_ME: {
            return {...state, email: action.email, password: action.password, rememberMe: action.rememberMe, captcha: action.captcha}
        }
        default: {
            return state
        }
    }
}

export const loginMe = ({email, password, rememberMe, captcha}) => ({type: AUTH_ME, email, password, rememberMe, captcha});
