import React from 'react';
import { Field } from 'redux-form';
import { Input } from '../Textarea/formChilds';
export const Captcha = ({captcha}) => {
    return (
        <div>
            <img src={captcha} alt='captcha' />
            <Field component={Input} placeholder="Введите символы с картинки" name="captcha" required />
        </div>
    )
} 