let initialState = {
    messages: [
        { message: 'Sovok', id: 1},
        { message: 'Bil', id: 2 },
        { message: 'Shit', id: 3 }
    ],
    dialogs: [
        { id: 1, name: 'Alim' },
        { id: 2, name: 'Danilka' },
        { id: 3, name: 'Zhan' },
        { id: 4, name: 'Ilya' }
    ]
}

let ADD_NEW_MESSAGE = 'social-network/dialogsReducer/ADD-NEW-MESSAGE';

export const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NEW_MESSAGE: {
            return {...state, messages: [...state.messages, {id: 7, message: action.message}]
            }
        }
        default: 
            return state;
    }
}
export const addNewMessage = (message) => ({type: ADD_NEW_MESSAGE, message})