import { stopSubmit } from "redux-form";
import { authAPI, securityApi } from "../../../service/service";

let AUTH_USER = 'social-network/authReducer/AUTH-USER';
let CAPTCHA = 'social-network/authReducer/CAPTHCA';


let initialState = {
    userId: null,
    email: null,
    login: null,
    isLoading: false,
    isAuth: false,
    captchaUrl: null
}
export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case AUTH_USER : {
            const {userId, email, login, isAuth} = action
            return {...state, userId: userId, email: email, login: login, isAuth: isAuth }
        }
        case CAPTCHA : {
            return {...state, captchaUrl: action.captcha }
        }
        default: 
        return state;
    }
}
export const authUser = (userId, email, login, isAuth) => ({type: AUTH_USER, userId, email, login, isAuth});
export const setCaptcha = (captcha) => ({type: CAPTCHA, captcha});


export const auth = () => async (dispatch) => {
    let response = await authAPI.getAuth()
    if (response.resultCode === 0) {
        const {id, email, login} = response.data;
        dispatch(authUser(id, email, login, true));
    }
}

export const login = (email, password, rememberMe, captcha) => async (dispatch) => {
    let response = await authAPI.loginMe(email, password, rememberMe, captcha)
    console.log(response)
    if(response.resultCode === 0) {
        dispatch(auth());
    } else {
        if (response.resultCode === 10) {
            dispatch(getCaptcha())
        }
        let message = response.messages.length > 0 ? response.messages[0] : 'Login Error, pls check ur data';
        dispatch(stopSubmit('login', {_error: message}));
    }
}

export const logout = () => async (dispatch) => {
    let response =  await authAPI.logoutMe()
    if(response.resultCode === 0) {
        dispatch(authUser(null, null, null, false))
    }
}

export const getCaptcha = () => async (dispatch) => {
    let response = await securityApi.getCapchaUrl();
    console.log(response)
    let captchaUrl = response.data.url; 
    dispatch(setCaptcha(captchaUrl))
}
