import React from 'react';
import { NavLink } from 'react-router-dom';
import sBarStyles from './navbar.module.scss';

const Navbar = () => {
    return (
        <aside className={sBarStyles.sidebar}>
            <ul>
                <li><NavLink activeClassName={sBarStyles.active} to="/profile">Моя страница</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/feed">Новости</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/messages">Сообщения</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/users">Пользователи</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/settings">Настройки</NavLink></li>
            </ul>
        </aside>
    )
}
export default Navbar;