import { profileAPI } from "../../../service/service";
import { likeHelper } from "../../../utils/objectHelpers/objectHelpers";

export let ADD_POST = 'social-network/profileReducer/ADD-POST';
let DELETE_POST = 'social-network/profileReducer/DELETE-POST';
let CHANGE_TEXT = 'social-network/profileReducer/CHANGE-POST-TEXT';
let GET_PROFILE = 'social-network/profileReducer/GET-PROFILE';
let TOGGLE_EDIT_MODE = 'social-network/profileReducer/TOGGLE-EDIT-MODE';
let NEW_STATUS_TEXT = 'social-network/profileReducer/NEW-STATUS-TEXT';
let SET_PHOTO = 'social-network/profileReducer/SET_PHOTO';
let SET_LIKE_POST = 'social-network/profileReducer/SET_LIKE_POST';
let UNSET_LIKE_POST = 'social-network/profileReducer/UNSET_LIKE_POST';
let PICTURE_PREVIEW = 'social-network/profileReducer/PICTURE_PREVIEW';



let initialState = {
    posts: [
        { id: 4, message: 'new post', likes: 13, liked: false, picture: null },
        { id: 3, message: 'TEST POSTS', likes: 12, liked: true, picture: null },
        { id: 2, message: 'TEST POSTS', likes: 15, liked: false, picture: null },
        { id: 1, message: 'TEST POSTS', likes: 16, liked: true, picture: null },
    ],
    profile: null,
    status: null,
    picturePreview: null
}

export const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let maxId = state.posts.unshift() + 1;
            let newPost = {
                id: maxId,
                message: action.text,
                likes: 0, 
                picture: state.picturePreview
            };
            return {...state, posts:[newPost, ...state.posts], picturePreview: null}
        }
        case DELETE_POST: {
            let index = state.posts.findIndex( post => post.id === action.postId)
            let newArr = [...state.posts.slice(0, index), ...state.posts.slice(index + 1)] 
            return {...state, posts: newArr}
        }
        case SET_LIKE_POST : {
            return {...state, posts: likeHelper(state.posts, action.postId, {liked: true}, {likes: action.count + 1}, 'id')}
        }
        case UNSET_LIKE_POST : {
            return {...state, posts: likeHelper(state.posts, action.postId, {liked: false}, {likes: action.count - 1}, 'id')}
        }
        case PICTURE_PREVIEW : {
            return {...state, picturePreview: action.file}
        }
        /* case CHANGE_TEXT: {
            return {...state,newPostText: action.newText}
        } */
        case GET_PROFILE: {
            return {...state, profile: action.profileId}
        }
        /* case TOGGLE_EDIT_MODE: {
            return {...state, editMode: action.editMode}
        } */
        case NEW_STATUS_TEXT: {
            return {...state, status: action.text}
        }
        case SET_PHOTO: {
            return {...state, profile: {...state.profile, photos: action.file}}
        }
        default: {
            return state;
        }
    }
}

export const addPost = (text, picture) => ({ type: ADD_POST, text, picture });
export const deletePost = (postId) => ({ type: DELETE_POST, postId });
export const likePost = (postId, count) => ({type: SET_LIKE_POST, postId, count});
export const disLikePost = (postId, count) => ({type: UNSET_LIKE_POST, postId, count});
export const setPicturePreview = (file) => ({type: PICTURE_PREVIEW, file})
export const changePostText = (text) => ({ type: CHANGE_TEXT, newText: text });
export const getProfile = (profileId) => ({type: GET_PROFILE, profileId});
export const toggleEditMode = (boolean) => ({type: TOGGLE_EDIT_MODE, editMode: boolean});
export const newStatusText = (text) => ({type: NEW_STATUS_TEXT, text});
export const savePhotoSucces = (file) => ({type: SET_PHOTO, file});


export const setProfile = (id) => async (dispatch) => {
    let response = await profileAPI.getMyProfile(id)
    dispatch(getProfile(response));
}
export const setStatus = (id) => async (dispatch) => {
    let response = await profileAPI.getStatus(id)
    dispatch(newStatusText(response));    
}
export const updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
    if(response.data.resultCode === 0) {
        dispatch(newStatusText(response));
    }
}
export const sendPhoto = (file) => async (dispatch) => {
    let response = await profileAPI.takePhoto(file)
    if(response.resultCode === 0) {
        dispatch(savePhotoSucces(response.data.photos))
    }
}
export const updateProfileData = (data) => async (dispatch, getState) => {
    const userId = getState().authReducer.userId
    let response = await profileAPI.sendProfileData(data)
    if(response.resultCode === 0) {
        dispatch(setProfile(userId))
    }
}