import { auth } from "../authReducer/authReducer";

let INITIALIZED_SUCCES = 'social-network/appReducer/INITIALIZED-SUCCES';

let initialState = {
    initialazed: false
}
export const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case INITIALIZED_SUCCES : {
            return {...state, initialazed:  true}
        }
        default: 
        return state;
    }
}
export const setInitializedSecces = () => ({type: INITIALIZED_SUCCES});


export const initializeApp = () => async (dispatch) => {
    let promise = await dispatch(auth())
    Promise.all([promise])
    dispatch(setInitializedSecces())
}

