import { applyMiddleware, combineReducers, createStore, compose } from "redux";
import { dialogsReducer } from "./reducers/dialogsReducer/dialogsReducer";
import { ADD_POST, profileReducer } from "./reducers/profileReducer/profileReducer";
import { loginReducer } from "./reducers/loginReducer/loginReducer";
import { usersReducer } from "./reducers/usersReducer/usersReducer";
import { appReducer } from "./reducers/appReducer/appReducer";
import { authReducer } from "./reducers/authReducer/authReducer";
import thunk from 'redux-thunk';
import { reducer as formReducer } from 'redux-form'


let reducers = combineReducers({
    authReducer,
    profileReducer,
    dialogsReducer,
    usersReducer,
    loginReducer,
    appReducer,
    form: formReducer.plugin({
        'post-form': (state, action) => {
            switch(action.type) {
                case ADD_POST: {
                    return undefined;
                }
                default : {
                    return state;
                }
            }
        }
    })
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(applyMiddleware(thunk)));

export default store;