import React, { Component } from 'react';
import Settings from './Components/Settings';
// import MessagesContainer from './Components/Messages';
// import ProfileContainer from './Components/Content';
// import Feed from './Components/Feed';
import Navbar from './Components/Navbar';
import styles from './index.module.scss';
import { Redirect, Route, Switch, withRouter } from 'react-router-dom';
// import UsersContainer from './Components/Users/usersContainer';
import HeaderContainer from './Components/Header/headerContainer';
import LoginPage from './Components/loginPage';
import { connect } from 'react-redux';
import {initializeApp} from './redux/reducers/appReducer/appReducer';
import { compose } from 'redux';
import Loading from './common/loading';
import { initialazedSelector } from './redux/selector/appSelector';
import { HashRouter } from 'react-router-dom';
import {Provider} from 'react-redux';
import store from './redux/reduxStore';
import { withSuspense } from './Components/hoc/reactSuspense';
import NotFound from './Components/404/404';
const MessagesContainer = React.lazy(() => import('./Components/Messages/messagesContainer'));
const ProfileContainer = React.lazy(() => import('./Components/Content/profileContainer'));
const UsersContainer = React.lazy(() => import('./Components/Users/usersContainer'));
const Feed = React.lazy(() => import('./Components/Feed'));

class App extends Component {
	catchAllUnhandleErrors = (promiseRejectionEvent) => {
		alert('Some error occured', promiseRejectionEvent.reason.response);
		console.log(promiseRejectionEvent.reason.response);
	}
	componentDidMount() {
        this.props.initializeApp();
		window.addEventListener('unhandledrejection', this.catchAllUnhandleErrors)
	}
	componentWillUnmount() {
		window.removeEventListener('unhandledrejection', this.catchAllUnhandleErrors)
	}
	render() {
		if (!this.props.initialazed) {
			return <Loading/>
		}
		return (
			<div className={styles.wrapper}>
				<HeaderContainer />
				<Navbar />
				<main className={styles.wrapper__content}>
					<Switch>
						<Route exact path="/" render={() => <Redirect to={'profile'} />} />
						<Route path="/login" render={() => <LoginPage />} />
						<Route path="/profile/:userId?" render={withSuspense(ProfileContainer)}/>
						<Route path="/feed" render={withSuspense(Feed)}/>
						<Route path="/messages" render={withSuspense(MessagesContainer)}/>
						<Route path="/users" render={withSuspense(UsersContainer)}/>
						<Route path="/settings" render={() => <Settings />} />
						<Route path="*" render={() => <NotFound />} />
					</Switch>
				</main>
			</div>
		)

	}
}

const mapStateToProps = (state) => {
	return {initialazed: initialazedSelector(state)}
}
let AppContainer = compose(
	withRouter,
	connect(mapStateToProps, {initializeApp})
)(App);

const MainApp = () => {
	return (
		<HashRouter basename={process.env.PUBLIC_URL}>
			<Provider store={store}>
				<AppContainer />
			</Provider>
		</HashRouter>
	)
}
export default MainApp;