import { usersAPI } from "../../../service/service";
import { userHelper } from "../../../utils/objectHelpers/objectHelpers";

let FOLLOW = 'social-network/usersReducer/FOLLOW';
let UNFOLLOW = 'social-network/usersReducer/UNFOLLOW';
let SET_USERS = 'social-network/usersReducer/SET-USERS';
let UPDATE_PAGES = 'social-network/usersReducer/UPDATE-PAGES';
let LOAD_USERS = 'social-network/usersReducer/LOAD-USERS';
let IS_FETCHING = 'social-network/usersReducer/IS-FETCHING';
let FOLLOW_IN_PROGRESS = 'social-network/usersReducer/FOLLOW-IN-PROGRESS'; 


let initialState = {
    users: [
        /* {
            id: 1,
            name: 'Roman Pshenichnyy', 
            avatar: 'https://sun3-13.userapi.com/impg/ZtquHClbbXaBoSccQqAIAdS02sEgrlDo8684-w/F-SaUcm0Usw.jpg?size=777x854&quality=96&sign=7e3c831a5cceac7f991054e603e51cc0&type=album', 
            city: 'Almaty', 
            cereer: 'Web-Developer', 
            status: "I'm Happy", 
            followed: true
        },
        { 
            id: 2, 
            name: 'Alimkhan Rathanov', 
            avatar: 'https://sun9-7.userapi.com/impf/qcm-11a4_k4hHU7mLPWrlsJx6DqI7wGsWy-tYA/DHXZrzilWDA.jpg?size=960x1280&quality=96&sign=fdfd71663547b66fadd666406cb5e300&type=album', 
            city: 'Almaty', 
            cereer: 'Economics', 
            status: "I'm Rofler", 
            followed: true 
        },
        {
            "name": "tire1315",
            "id": 16042,
            "uniqueUrlName": null,
            "photos": {
              "small": null,
              "large": null
            },
            "status": null,
            "followed": false
        } */
    ],
    isLoading: false,
    pageSize: 4,
    totalUserCount: 0,
    currentPage: 1,
    followingInProgress: []
};
export const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW: {
            return {
                ...state,
                users: userHelper(state.users, action.userId, {followed: true}, 'id')
            }
        }
        case UNFOLLOW: {
            return {
                ...state,
                users: userHelper(state.users, action.userId, {followed: false}, 'id')
            }
        }
        case SET_USERS: {
            return { ...state, users: [...action.users], }
        }
        case UPDATE_PAGES: {
            return { ...state, currentPage: action.pageNumber }
        }
        case LOAD_USERS: {
            return { ...state, totalUserCount: action.usersAmount }
        }
        case IS_FETCHING: {
            return { ...state, isLoading: action.loading }
        }
        case FOLLOW_IN_PROGRESS: {
            return { 
                ...state, 
                followingInProgress: action.status 
                ? [...state.followingInProgress, action.userId] 
                : state.followingInProgress.filter(id => id !== action.userId)
            }
        }
        default: {
            return state;
        }
    }
}

export const Follow = (userId) => ({ type: FOLLOW, userId });
export const Unfollow = (userId) => ({ type: UNFOLLOW, userId });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const updatePages = (pageNumber) => ({ type: UPDATE_PAGES, pageNumber });
export const loadUsers = (usersAmount) => ({ type: LOAD_USERS, usersAmount });
export const loader = (loading) => ({ type: IS_FETCHING, loading });
export const followProgress = (status, userId) => ({ type: FOLLOW_IN_PROGRESS, status, userId });

export const getUsers = (currentPage, pageSize) => async (dispatch) => {
    dispatch(loader(true));
    let response = await usersAPI.getUsers(currentPage, pageSize);
    dispatch(setUsers(response.items));
    dispatch(loadUsers(response.totalCount = 499));
    dispatch(loader(false));
}

export const getPages = (currentPage, pageSize) => async (dispatch) => {
    dispatch(updatePages(currentPage));
    let response = await usersAPI.getUsers(currentPage, pageSize)
    dispatch(setUsers(response.items));
}


const followUnfollowFlow = async (apiMethod, actionCreator, id, dispatch) => {
    dispatch(followProgress(true, id));
    let response = await apiMethod(id);
    if (response.resultCode === 0) {
        dispatch(actionCreator(id));
        dispatch(followProgress(false, id));
    }
}
export const followThunk = (id) => async (dispatch) => {
    followUnfollowFlow(usersAPI.follow.bind(usersAPI), Follow, id, dispatch);
}
export const unFollowThunk = (id) => async (dispatch) => {
    followUnfollowFlow(usersAPI.unFollow.bind(usersAPI), Unfollow, id, dispatch);
}