import React from 'react';
import { Redirect } from 'react-router';
import { Field, reduxForm } from 'redux-form';
import { Captcha } from '../../common/capcha/captcha';
import { Input } from '../../common/Textarea/formChilds';
import {maxLengthCreator, requiredField} from '../../utils/validator';
import s from './formValidate.module.scss';

let maxLength20 = maxLengthCreator(20)
let LoginForm = ({handleSubmit, error, captcha}) => {
    return (
        <form className="form" onSubmit={handleSubmit}>
            <div>
                <Field component={Input} type="email" validate={[requiredField, maxLength20]} name="email" placeholder="Почта"/>
            </div>
            <div>
                <Field component={Input} validate={[requiredField, maxLength20]} name="password" placeholder="Пароль" type="password" />
            </div>
            <div>
                <Field component="input" name="rememberMe" type='checkbox'/>
            </div>
            {captcha ? <Captcha captcha={captcha} /> : null}
            {
                error ? 
                    <div className={s.formAccesError}>
                        {error}
                    </div>: 
                null
            }
            <div>
                <button type="submit">Войти</button>
            </div>
        </form>
    )
}
LoginForm = reduxForm({
    form: 'login'
})(LoginForm)
const LoginPage = ({login, isAuth, captchaUrl}) => {
    const submit = (values) => {
        const {email, password, rememberMe, captcha} = values;
        console.log(values)
        login(email, password, rememberMe, captcha);
    }
    if (isAuth) {
        return <Redirect to={"/profile"} />
    }
    return (
        <div>
            <LoginForm onSubmit={submit} captcha={captchaUrl} />
        </div>
    )
}
export default LoginPage;