import {connect} from 'react-redux';
import {authUser, logout} from '../../redux/reducers/authReducer/authReducer';
import { isLoadingSelector, loginSelector, userIdSelector, isAuthSelector, emailSelector } from '../../redux/selector/authSelector';
import HeaderService from './headerService';
const mapStateToProps = (state) => {
    return {
        userId: userIdSelector(state),
        email: emailSelector(state),
        login: loginSelector(state),
        isLoading: isLoadingSelector(state),
        isAuth: isAuthSelector(state)
    }
} 
const HeaderContainer = connect(mapStateToProps, {authUser, logout})(HeaderService);
export default HeaderContainer;