import React from 'react';
import fStyles from './formChilds.module.scss';

export let FormControl = ({meta, children}, props) => {
    const {error, touched} = meta;
    return (
        <div className={touched && error ? fStyles.formControl_error : fStyles.formControl}>
            {children}
            <div className={fStyles.errorBlock}>{error}</div>
        </div>
    )
}
export let TextArea = (props) => {
    const {input, meta, children,  ...restProps} = props;
    return (
        <FormControl {...props}>
            <textarea {...input} {...restProps} placeholder={props.placeholder} />
        </FormControl>
    )
}
export let Input = (props) => {
    const {input, meta, children, ...restProps} = props;
    return (
        <FormControl {...props}>
            <input {...input} {...restProps} placeholder={props.placeholder} />
        </FormControl>
    )
}